import React from 'react'
import PropTypes from 'prop-types'

const Mobile = (props, context) => {
  return <div className="merchant-container">
    {props.children}
  </div>
}

Mobile.propTypes = {
  children: PropTypes.element
}

export default store => ({
  path: 'merchant',
  component: Mobile,
  getChildRoutes (partialNextState, cb) {
    require.ensure([], (require) => {
      cb(null, [
        require('./routes/H5Creator').default(store),
        require('./routes/Editor').default(store),
        require('./routes/H5Editor').default(store),
        require('./routes/Member').default(store),
        require('./routes/TemplateRouter').default(store),
        require('./routes/UserPreivew').default(store),
        require('./routes/Home').default(store),
        require('./routes/ProbeReport').default(store),
        require('../Home/index').default(store),
        require('./routes/H5BindCount').default(store),
        require('./routes/H5MemberCenter').default(store),
        require('./routes/H5MyOrderList').default(store),
        require('./routes/H5Order').default(store)
      ])
    })
  }
})



